import React from "react";
import "./SoftwareSkill.scss";
import {skillsSection} from "../../portfolio";
import {ReactComponent as Flutter} from "../../assets/images/flutter.svg";
import {ReactComponent as CSharp} from "../../assets/images/csharp.svg";

export default function SoftwareSkill() {
  return (
    <div>
      <div className="software-skills-main-div">
        <ul className="dev-icons">
          {skillsSection.softwareSkills.map((skills, i) => {
            return skills?.willUseSVG ? (
              <li
                key={i}
                className="software-skill-inline"
                name={skills.skillName}
              >
                {(() => {
                  switch (skills.svgSrc) {
                    case "flutter":
                      return <Flutter width={40} height={40} />;
                    case "csharp":
                      return <CSharp width={40} height={40} />;
                    default:
                      return <div></div>;
                  }
                })()}
                <p>{skills.skillName}</p>
              </li>
            ) : (
              <li
                key={i}
                className="software-skill-inline"
                name={skills.skillName}
              >
                <i className={skills.fontAwesomeClassname}></i>
                <p>{skills.skillName}</p>
              </li>
            );
          })}
        </ul>
      </div>
    </div>
  );
}
